pre {
    background: #f4f5f6;
    border-left: 0.3rem solid #4682b4;
    font-family: Menlo, Consolas, "Bitstream Vera Sans Mono", "DejaVu Sans Mono", Monaco, monospace;
}

a {
    color: #4682b4;
    text-decoration: none;
}

.text-center {
    text-align: center;
}

select:focus {
    border: 0.1rem solid #4682b4;
    outline: 0;
}

hr {
    border-top: 0.1rem solid #4682b4;
    border-width: 0.1rem;
    margin-bottom: 3.5rem;
    margin-top: 3rem;
}

input[type="submit"].button-clear {
    color: #4682b4;
    background-color: transparent;
    border-color: transparent;
}
.button.button-outline,
button.button-outline,
input[type="button"].button-outline,
input[type="reset"].button-outline,
input[type="submit"].button-outline {
    color: #4682b4;
    background-color: transparent;
}
.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
input[type="submit"] {
    background-color: #4682b4;
    border: 0.1rem solid #4682b4;
    border-radius: 0.4rem;
    box-sizing: border-box;
    color: #ffffff;
    cursor: pointer;
    display: inline-block;
    font-size: 1.1rem;
    font-weight: 700;
    height: 3.8rem;
    letter-spacing: 0.1rem;
    line-height: 3.8rem;
    padding: 0 3rem;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
}

.button.button-warning,
button.button-warning,
input[type="button"].button-warning,
input[type="reset"].button-warning,
input[type="submit"].button-warning,
input[type="submit"].button-warning {
    background-color: #F9C935;
    border-color: #F9C935;
    color: #3d3d3d
}

.button.button-success,
button.button-success,
input[type="button"].button-success,
input[type="reset"].button-success,
input[type="submit"].button-success,
input[type="submit"].button-success {
    background-color: #41BB8A;
    border-color: #41BB8A;
}

.button-danger {
    background-color: #FF8A58;
    border-color: #FF8A58;
}

input[type="text"]:focus,
input[type="url"]:focus,
textarea:focus,
select:focus {
    border: 0.1rem solid #4682b4;
    outline: 0;
}

.callout {
    border: 0px solid #e9ecef;
    border-left-width: 5px;
    padding: 18px;
    margin: 10px 0;
    background-color: #f4f5f6;
}
.callout .title {
    font-weight: bold;
}

.callout-info {
    border-left-color: #6AA0CC;
}
.callout-success {
    border-left-color: #41BB8A;
}
.callout-warning {
    border-left-color: #F9C935;
}
.callout-danger {
    border-left-color: #FF8A58;
}

div.section {
    padding: 15px 0;
}

div.section.gray {
    background-color: #f4f5f6;
}

.progress {
    border-radius: 11px;
    height: 22px;
    width: 100%;
    background-color: #CAE2F5;
}
.progress .bar {
    border-radius: 11px;
    width: 0%;
    background-color: #4682b4;
    height: 100%;
    color: white;
    text-align: center;
    font-weight: 700;
}

div.stats {
   width: auto;
   height: auto;
   position: fixed;
   bottom: 0px;
   left: 0px;
   background-color: #f4f5f6;
   padding: 10px;
}
div.stats p {
    margin: 0;
    padding: 0;
    font-size: 80%;
    font-weight: 700;
}
div.stats p span {
    font-weight: normal;
}

#programButton::after {
    content: '';
    display: block;
    position: relative;
    width: 50%;
    height: 100%;
    background-color: red;
    opacity: 0.3;
}

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  .accordion {
    background-color: #f4f5f6;
    color: #606c76;
    cursor: pointer;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    transition: 0.4s;
    margin: 15px 0 15px 0;
  }
  
  .active, .accordion:hover {
    background-color: #ccc;
  }
  .active {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    margin: 15px 0 0 0;
  }
  
  /* Style the accordion panel. Note: hidden by default */
  .panel {
    padding: 18px 28px;
    background-color: #f4f5f6; /*white;*/
    display: none;
    overflow: hidden;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    margin-bottom: 20px;
  }
  .panel img {
    border-radius: 12px;
  }
  .panel p {
    margin-bottom: 0;
  }